import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NbToastrService } from '@nebular/theme';
import { BadgesServices } from '../../../services/badges/badges.services';
import { StoresServices } from '../../../services/stores/stores.services';
@Component({
  selector: 'store-credentials',
  templateUrl: './store-credentials.component.html',
  styleUrls: ['./store-credentials.component.scss']
})
export class StoreCredentialsComponent implements OnInit {

  public dropzoneConfig: any = {
    paramName: 'file', // The name that will be used to transfer the file
    maxFilesize: 2, // MB
    autoQueue: false,
    url: '/',
    autoProcessQueue: false,
    multi: false
  };
  title: string;
  storeFormGroup: FormGroup;
  selectedLogo: File;
  selectedLicence: File;
  isLoading = false;
  types: any[] = [];
  badgesList: any[] = [];
  storeId: any;
  store_type;

  constructor(
    private storeServices: StoresServices,
    private badgesServices: BadgesServices,
    private activeRouter: ActivatedRoute,
    private router: Router,
    private toaster: NbToastrService,
  ) {
    this.storeFormGroup = new FormGroup({
      id: new FormControl(),
      email: new FormControl(null, Validators.required),
      mobile: new FormControl(null, Validators.required),
      password: new FormControl(null),
    });
  }

  ngOnInit(): void {
    this.store_type = this.router.url.split('/')[1];
    this.activeRouter.params.subscribe(v => this.storeId = v.id);
    if (this.storeId) {
      this.title = 'Edit';
      this.getStore();
    } else {
      this.title = 'Add';
    }
  }

  getStore() {
    this.isLoading = true;
    this.storeServices.getStoreCredentials(this.storeId).subscribe(
      res => {
        this.storeFormGroup.get('email').setValue(res.data['email']);
        this.storeFormGroup.get('mobile').setValue(res.data['mobile']);
        this.storeFormGroup.get('id').setValue(res.data['id']);
      },
      error => {
        this.isLoading = false;
      },
      () => {
        this.isLoading = false;
      },
    )
  }

  onStorLogoChange(event: any) {
    this.selectedLogo = event;
  }

  onStorLicenceChange(event: any) {
    this.selectedLicence = event;
  }

  updateStore() {
    if (this.storeFormGroup.valid) {
      this.isLoading = true;
      const uploadData: FormData = new FormData();
      const groupFormData: any = this.storeFormGroup.value;
      uploadData.append('owner_id', groupFormData.id);
      uploadData.append('email', groupFormData.email);
      uploadData.append('mobile', groupFormData.mobile);
      if (groupFormData.password)
        uploadData.append('password', groupFormData.password);
      this.storeServices.updateStoreAuth(uploadData).subscribe(
        res => {
          this.toaster.show(res['msg'], 'Success', { status: 'success' });
          this.getStore();
          this.router.navigateByUrl('/' + this.store_type);
        },
        error => {
          this.toaster.show(error.error.message, 'Error', { status: 'danger' });
          this.isLoading = false;
        },
        () => {
          this.isLoading = false;
        },
      )

    }
  }

  getBadgesList() {
    this.badgesServices.getBadgesList("?is_store=true").subscribe(
      res => {
        this.badgesList = res.data;
      },
      error => { },
      () => { },
    )
  }

  getTypes() {
    this.storeServices.getStoreTypes().subscribe(
      res => {
        this.types = res.store_types;
      },
      error => { },
      () => { },
    )
  }

  backToStores() {
    this.router.navigate([ this.store_type])
  }
}
