import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NbToastrService } from '@nebular/theme';
import { SharedApiServiceService } from '../../../services/shared/shared-api-service.service';
import { StoresServices } from '../../../services/stores/stores.services';
import { MapComponent } from '../map/map.component';

@Component({
  selector: 'store-address',
  templateUrl: './store-address.component.html',
  styleUrls: ['./store-address.component.scss']
})
export class StoreAddressComponent implements OnInit {

  @ViewChild('addressMap') addressMap: MapComponent;

  storeAddressFormGroup: FormGroup;
  cities: any[];
  isLoading: boolean = false;
  storeId: any;
  constructor(
    private storeServices: StoresServices,
    private activeRouter: ActivatedRoute,
    private route: Router,
    private toaster: NbToastrService,
    private sharedApi: SharedApiServiceService
  ) {
    this.storeAddressFormGroup = new FormGroup({
      "seller_id": new FormControl(),
      "address": new FormControl(null, Validators.required),
      "city_id": new FormControl(null, Validators.required),
      "latitude": new FormControl(),
      "longitude": new FormControl(),
      "building_no": new FormControl(null, Validators.required),
      "landmark": new FormControl(),
      "main_street": new FormControl(null, Validators.required),
      "side_street": new FormControl(),
    })
  }

  ngOnInit(): void {
    this.activeRouter.params.subscribe(v => this.storeId = v.id);
    if (this.storeId) {
      this.getAllCities();
      this.getAddress();
    }
  }

  getAddress() {
    this.isLoading = true;
    this.storeServices.getStorAddress(this.storeId).subscribe(
      res => {
        this.storeAddressFormGroup.setValue(res.data);
        this.addressMap.setLatLng(res.data);
      },
      error => {
        this.isLoading = false;
      },
      () => {
        this.isLoading = false;
      },
    )
  }

  updateAddress() {
    debugger
    if (this.storeAddressFormGroup.valid) {
      this.isLoading = true;
      let formValue: any = this.storeAddressFormGroup.value;
      formValue.latitude = this.addressMap.getLatLng().lat;
      formValue.longitude = this.addressMap.getLatLng().lng;
      this.storeServices.updateStoreAddress(formValue).subscribe(
        res => {
          this.toaster.show(res['message'], 'Success', { status: 'success' });
          this.route.navigateByUrl('/stores');
        },
        error => {
          this.toaster.show(error.error.message, 'Error', { status: 'danger' });
          this.isLoading = false;
        },
        () => {
          this.isLoading = false;
        },
      )
    }
  }

  getAllCities() {
    this.sharedApi.getCities().subscribe(
      res => {
        this.cities = res.data;
      },
      error => { },
      () => { },
    )
  }

  backToStores() {
    this.route.navigate(['stores'])
  }

}
