import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { GoogleMap } from '@angular/google-maps';

@Component({
  selector: 'address-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss']
})
export class MapComponent implements OnInit {
  @ViewChild(GoogleMap) googleMap: GoogleMap;
  @Input() zoom: number = 5;
  @Input() center = { lat: 30.033333, lng: 31.233334 };


  public markerPins: google.maps.LatLngLiteral[] | any[] = [];

  public options: any = {
    styles: [],
    zoomControl: true,
    mapTypeControl: false,
    scaleControl: false,
    streetViewControl: false,
    rotateControl: false,
    fullscreenControl: false,
    gestureHandling: 'cooperative'
  };
  constructor() { }

  ngOnInit(): void {
  }

  getLatLng() {
    return this.markerPins[0];
  }

  setLatLng(data: any) {
    this.markerPins = [];
    const latLang = {
      lat: data.latLng ? data.latLng.lat() : parseFloat(data.latitude),
      lng: data.latLng ? data.latLng.lng() : parseFloat(data.longitude),
      option: {
        draggable: false,
      }
    }

    this.markerPins.push(latLang);
  }

}
