import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { SidebarComponent } from '../../shared/sidebar/sidebar.component';
import { IndexStoresComponent } from './index-stores/index-stores.component';
import { AddEditStoreComponent } from './add-edit-store/add-edit-store.component';
import { OnlyLoggedInUsers } from '../../guards/only-logged-in-users.guard';

const routes: Routes = [{
    path: '',
    component: SidebarComponent,
    children: [
        { path: '', component: IndexStoresComponent, data: { is_verified: 1 } },
        { path: 'pending', component: IndexStoresComponent, data: { is_verified: 0 } },
        { path: 'create-store', component: AddEditStoreComponent },
        { path: 'edit/:id', component: AddEditStoreComponent },
        {
            path: 'attachments',
            loadChildren: () => import('./attachments/attachments.module').then(m => m.AttachmentsModule), canActivate: [OnlyLoggedInUsers]
        },
    ],
}];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class StoresRoutingModule {
}